// app/store/[id]/page.tsx
'use client' // اگر از هوک‌ها یا state استفاده می‌کنید

import Prd from '@/components/Prd'

// راه‌حل طلایی: استفاده از انواع پیش‌فرض Next.js
export default function Page({ 
  params 
}: { 
  params: { 
    id: string 
  } 
}) {
  return (
    <div>
      <Prd productId={params.id} />
    </div>
  )
}